.filter {
    width: 80%;
    height: 100vh;
    max-width: 28rem;
    background-color: #192851;
    color: white;
    position: fixed;
    top: 0;
    right: 0;
    border-radius: 10px 0 0 10px;
    box-shadow: -4px 0 12px 0 rgba(125, 219, 185, 0.7);
    z-index: 200;
    overflow: scroll;
    // 不要出現捲軸
    &::-webkit-scrollbar {
      width: 0;
    }
  
    &__container {
      padding: 2rem;
    }
  
    &__header {
      &-container {
        margin-top: 2rem;
        padding: 0 1rem;
        width: 100%;
        display: flex;
      }
    }
  
    h2 {
      color: white;
      font-weight: bold;
      font-size: 1.15rem;
      margin: 0 0 0.5rem 0;
    }
  
    &__button {
      &-container {
        margin: 0 0 1rem 0;
        height: 100%;
        width: 100%;
        display: flex;
        gap: 1rem;
        flex-wrap: wrap;
        justify-content: flex-start;
      }
  
      &-items {
        border-radius: 5px;
        height: 100%;
        :hover{
          background-color: #FF7B7B;
          border: 2px solid #FF7B7B;
          color: white;
        }
      }
  
      &-item {
        display: flex;
        justify-content: center;
        align-items: center;
        // font-size: 1rem;
        min-width: 6rem;
        height: 2rem;
        border: 1px solid white;
        border-radius: 15px;
        color: white;
        flex-wrap: wrap;
        svg{
          width: 1.25rem;
        }
      }
  
      &-showDistrict {
        background-color: white;
        color: #192851;
        width: 6rem;
        height: 2rem;
        border-radius: 15px;
      }
  
      &-cancel {
        &-container {
          width: 100%;
          padding-left: 2rem;
        }
        color: white;
        border: 2px solid white;
        width: 9rem;
        height: 2rem;
        border-radius: 15px;
        &:hover{
          background-color: #FF7B7B;
          border: 2px solid #FF7B7B;
          color: white;
        }
      }
    }
  
    .remaining {
      width: 13rem;
      .filter__button {
        &-unactive {
          color: white;
          border: 2px solid white;
          width: 6rem;
          height: 2rem;
          border-radius: 15px;
          &:hover{
            background-color: #FF7B7B;
            border: 2px solid #FF7B7B;
            color: white;
          }
        }
  
        &-showRemaining,
        &-notShowRemaining {
          background-color: white;
          color: #192851;
          width: 6rem;
          height: 2rem;
          border-radius: 15px;
        }
      }
    }
  }
  
  @media screen and (min-width: 992px) {
    .filter {
      width: 40%;
      
    }
  }
  