.search{
    &__container{
      position: fixed;
      top: 2rem;
      left: 50%;
      transform: translate(-50%);
      width: 60%;
      height: 2rem;
      z-index: 100;
      max-width: 35rem;
    }
    
    &__input{
      // 輸入匡背景的樣式
      width: 100%;
      height: 100%;
      color: #696974;
      border-radius: 15px;
      border: 2px solid #192851;
      box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.2);
      background-color: #FFF;
      ::placeholder{
        color: #192851;
      }
  
      &-container{
        width: 100%;
        height: 100%;
        position: relative;
      }
  
      input{
        // 輸入匡的樣式
        color: #000;
        width: 100%;
        line-height: 2rem;
        margin-left: 1rem;
      }
  
      &-lists{
        margin-left: 1rem ;
        width: 90%;
        background-color: #FFF;
        box-shadow: 0 10px 12px 0 rgba(0, 0, 0, 0.2);
        border-radius: 0 0 5px 5px;
      }
  
      &-list{
        display: flex;
        font-size: 0.85rem;
        line-height: 1.35rem;
        width: 100%;
        text-align: start;
        // border-bottom: 1px solid grey;
        &:hover{
          background-color: #192851;
          color: white;
        }
        
  
        &_SVGParking{
          height: 1rem;
          width: 1rem;
          margin: auto 0 ;
        }
  
        &_description{
          flex: 1;
        }
      }
  
      &-clearText {
        position: absolute;
        top: 0.375rem;
        right: 2rem;
        height: 1.25rem;
        width: 1.25rem;
      }
  
      &-find {
        position: absolute;
        top: 0.375rem;
        right: 0.5rem;
        height: 1.25rem;
        width: 1.25rem;
      }
  
      &-lastStep {
        position: absolute;
        top: -0.25rem;
        left: -3rem;
      }
  
      &-filter {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 2.25rem;
        width: 2.25rem;
        top: -0.125rem;
        right: -2.75rem;
        background-color: #192851;
        border-radius: 50%;
      }
  
      
    }
  }
  
  @media screen and (min-width: 992px) {
    .search{
    &__container{
      height: 2rem;
    }
    
    &__input{
      // 輸入匡背景的樣式
        box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.2);
        background-color: #FFF;
  
      &-container{
        width: 100%;
        height: 100%;
        position: relative;
      }
  
      input{
        // 輸入匡的樣式
        color: #000;
        width: 100%;
        line-height: 2rem;
        margin-left: 1rem;
      }
  
      &-lists{
        margin-left: 1rem ;
        width: 95%;
        background-color: #FFF;
        border-radius: 0 0 5px 5px;
      }
  
      &-list{
        display: flex;
        font-size: 1rem;
        line-height: 1.65rem;
        
  
        &_SVGParking{
          height: 1rem;
          width: 1rem;
          margin: auto 0 ;
        }
  
        &_description{
          flex: 1;
        }
      }
  
      &-clearText {
        position: absolute;
        top: 0.375rem;
        right: 2rem;
        height: 1.25rem;
        width: 1.25rem;
      }
  
      &-find {
        position: absolute;
        top: 0.375rem;
        right: 0.5rem;
        height: 1.25rem;
        width: 1.25rem;
      }
  
      &-lastStep {
        position: absolute;
        top: -0.25rem;
        left: -3rem;
      }
      
    }
  }
  }